import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Col, Form, Input, Checkbox, message } from 'antd';
import { BtnDelete, BtnAddNew } from 'components/common/button/index';
import {
  YearSemesterInput,
  SelectAjax,
  TableDataEditRow,
  DatePickerString,
} from 'components/common/index';
import { getPrizeClassification } from 'services/prizeTypeForAddScholarship';
import { getStudentCode, createprizeNotes } from 'services/addPrizeNote';

@Form.create({ name: 'formAddScholarship' })
class FormAddScholarship extends Component {
  state = {
    initialValue: {},
    dataAddNewScholarship: [],
  };

  componentDidMount = () => {};

  onChange = (val, index) => {
    const { value } = val.target;
    this.getInputCode(value, index);
  };

  getInputCode = async (val, index) => {
    try {
      const { dataAddNewScholarship } = this.state;
      const { value } = val.target;
      if (value.trim().length === 0) {
        const data = dataAddNewScholarship.map((item, indexItem) => {
          if (indexItem === index) {
            item.key = dataAddNewScholarship[index].key;
            item.code = '';
            item.studentId = '';
            item.fullname = '';
            item.dob = '';
            item.className = '';
            item.solutionLabel = dataAddNewScholarship[index].solutionLabel;
            item.solutionId = dataAddNewScholarship[index].solutionId;
            item.amount = dataAddNewScholarship[index].amount;
            item.isCorp = dataAddNewScholarship[index].isCorp;
          }
          return {
            key: item.key,
            code: item.code,
            studentId: item.studentId,
            fullname: item.fullname,
            dob: item.dob,
            className: item.className,
            solutionLabel: item.solutionLabel,
            solutionId: item.solutionId,
            amount: item.amount,
            isCorp: item.isCorp,
          };
        });
        this.setState({ dataAddNewScholarship: data, reload: this.state.reload + 1 });
      }
      if (value.trim().length > 0) {
        let dataStudentCode = '';
        try {
          dataStudentCode = await getStudentCode(value);
        } catch (err) {
          const { status } = JSON.parse(JSON.stringify(err)).response;
          if (status === 404) {
            message.warning('MSSV khng tn ti!');
          }
        }
        const data = dataAddNewScholarship.map((item, indexItem) => {
          if (indexItem === index) {
            item.key = dataAddNewScholarship[index].key;
            item.code = dataStudentCode.data.code;
            item.studentId = dataStudentCode.data.id;
            item.fullname = `${dataStudentCode.data.firstname} ${dataStudentCode.data.lastname}`;
            item.dob = dataStudentCode.data.dob;
            item.className = dataStudentCode.data.className;
            item.solutionLabel = dataAddNewScholarship[index].solutionLabel;
            item.solutionId = dataAddNewScholarship[index].solutionId;
            item.amount = dataAddNewScholarship[index].amount;
            item.isCorp = dataAddNewScholarship[index].isCorp;
          }
          return {
            key: item.key,
            code: item.code,
            studentId: item.studentId,
            fullname: item.fullname,
            dob: item.dob,
            className: item.className,
            solutionLabel: item.solutionLabel,
            solutionId: item.solutionId,
            amount: item.amount,
            isCorp: item.isCorp,
          };
        });
        this.setState({ dataAddNewScholarship: data, reload: this.state.reload + 1 });
      }
    } catch (err) {
      // console.log(err);
    }
  };

  getDataAddToTable = () => ({ data: { items: this.state.dataAddNewScholarship } });

  addRowDataTable = () => {
    const { dataAddNewScholarship } = this.state;
    const item = {
      key: new Date().getTime(),
      code: '',
      studentId: '',
      fullname: '',
      dob: '',
      className: '',
    };
    dataAddNewScholarship.push(item);
    this.setState({
      dataAddNewScholarship,
      reload: this.state.reload + 1,
    });
  };

  deleteRowDataTable = async key => {
    const { dataAddNewScholarship } = this.state;
    const data = dataAddNewScholarship.filter(item => item.key !== key);
    this.setState({
      dataAddNewScholarship: data,
      reload: this.state.reload + 1,
    });
  };

  handleCreateScholarship = async data => {
    await createprizeNotes(data);
    this.props.history.goBack();
    message.success('Thm thnh cng!');
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const { dataAddNewScholarship } = this.state;

      const dataDetailsRow = dataAddNewScholarship.map(item => {
        const data = {
          studentId: item.studentId,
          solutionId: item.solutionId,
          amount: item.amount,
          isCorp: item.isCorp,
        };
        if (data.studentId === '') {
          message.warning('Vui lng nhp MSSV!');
        }
        if (data.solutionId === undefined) {
          message.warning('Vui lng nhp hc bng!');
        }
        return data;
      });

      if (err) {
        return;
      }

      const dataCreate = { id: null, ...values, pnDetails: dataDetailsRow };
      this.handleCreateScholarship(dataCreate);
    });
  };

  updateDataRow = async (key, dataRow) => {
    const { dataAddNewScholarship } = this.state;
    const indexRow = dataAddNewScholarship.findIndex(item => item.key === key);
    dataAddNewScholarship[indexRow] = dataRow;
    this.setState({
      dataAddNewScholarship,
      reload: this.state.reload + 1,
    });
  };

  handleSaveRowData = async rowData => {
    const { objSelected, solutionId, amount, isCorp = false } = rowData;

    if (!solutionId || !amount) {
      message.warning('Vui lng in thng tin!');
      return null;
    }

    const newDataRow = {
      ...rowData,
      solutionId,
      solutionLabel: objSelected.solutionId.label,
      amount,
      isCorp,
    };

    await this.updateDataRow(rowData.key, newDataRow);
    return newDataRow;
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
        render: (text, record, index) => (
          <Input
            placeholder="Nhp MSSV"
            allowClear
            defaultValue={record.code}
            maxLength={10}
            onChange={e => this.onChange(e, index)}
            onBlur={e => this.getInputCode(e, index)}
          />
        ),
      },
      {
        title: 'H tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Loi hc bng',
        dataIndex: 'solutionLabel',
        width: '150px',
        key: 'solutionId',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('solutionId', {
              initialValue: record.solutionId,
            })(<SelectAjax form={form} service={getPrizeClassification} allowGetObjSelected />)}
          </Form.Item>
        ),
      },
      {
        title: 'S tin',
        dataIndex: 'amount',
        key: 'amount',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('amount', {
              initialValue: record.amount,
            })(<Input />)}
          </Form.Item>
        ),
      },
      {
        title: 'Hc bng doanh nghip',
        dataIndex: 'isCorp',
        key: 'isCorp',
        render: (text, record) => <Checkbox checked={record.isCorp} disabled />,
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('isCorp', {
              valuePropName: 'checked',
              initialValue: record.isCorp,
            })(<Checkbox />)}
          </Form.Item>
        ),
      },
    ];
    return (
      <Card>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
          <Row gutter={24}>
            <Col span={6}>
              <Form.Item label="S Q" hasFeedback>
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Ngy Q" hasFeedback>
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<DatePickerString placeholder="Chn ngy" />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Ni dung" hasFeedback>
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp thng tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <YearSemesterInput form={this.props.form} />
            <Col span={6}>
              <div className="btn-group">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </div>
            </Col>
          </Row>
        </Form>
        <Row>
          <BtnAddNew title="Thm hc bng" onClick={this.addRowDataTable} />
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={this.getDataAddToTable}
              columns={columns}
              handleSaveRowData={this.handleSaveRowData}
              customBtnAction={({ record, editable, btnAction }) => {
                if (record.disable) {
                  return null;
                }
                if (editable === true) {
                  return <Fragment>{btnAction}</Fragment>;
                }
                return (
                  <Fragment>
                    {btnAction}
                    <BtnDelete onClick={() => this.deleteRowDataTable(record.key)}>Xa</BtnDelete>
                  </Fragment>
                );
              }}
              rowKey="key"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddScholarship {...props} />
  </Page>
));



